.header {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  padding-top: 15px;
  padding-bottom: 15px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    display: flex;
    align-items: center;
    margin-right: 40px;
    & a {
      margin-right: 20px;
    }
    & > span {
      padding-top: 10px;
      max-width: 190px;
      font-size: 1.4rem;
    }
  }
  // &__desc {

  // }
  &__btn {
    max-width: 300px;
    & a {
      font-size: 1.4rem;
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: center;
      border: 1px solid #84d3ff;
      border-radius: 80px;
      padding: 8px 20px;
      transition: .25s;

    }
    &:hover a {
      background-color: #84d3ff;
      // color: ;
    }
    & img {
      margin-right: 5px;
    }
    &--catalog a {
      padding-left: 60px;
    }
    &--catalog a::before {
      content: url(../img/content/journal.png);
      position: absolute;
      left: 0;
      top: 0;
      transform: translateX(-50%) translateY(-20%);
    }
  }
  &__tel {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    & > a {
      font-size: 2.4rem;
      font-weight: 700;
      white-space: nowrap;
      &:hover {
        text-decoration: underline;
      }
    }
    & > span {
      font-size: 1.6rem;
      text-decoration: underline;
    }
  }
}