.footer {
  color: #1e3954;
  padding: 20px 100px 25px;
  font-size: 1.4rem;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }
  &__logo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    & > img {
      margin-right: 55px;
    }
    & > span {
      max-width: 190px;
    }
  }
  &__copyright {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }
  &__links {
    display: flex;
    align-items: center;
    & > a {
      text-decoration: underline;
      margin-left: 70px;
    }
  }
}