@charset "utf-8";
@import "reset";
@import "basic";
@import "blocks/header";

$accent-color: #ffb400;
$font: "GreycliffCF";

.first-screen {
  background: no-repeat center / cover;
  background-image: url(../img/bg/bg1.jpg);
  padding-top: 150px;
  padding-bottom: 35px;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__intro {
    font-size: 1.8rem;
    font-weight: 500;
    margin-bottom: 20px;
  }
  &__text {
    white-space: normal;
    max-width: 600px;
    font-size: 3rem;
    line-height: 40px;
    font-weight: 900;
    text-transform: uppercase;
    margin-bottom: 60px;
  }
  &__features {
    margin-bottom: 25px;
  }
  &__outro {
    font-size: 2.2rem;
    font-weight: 500;
    margin-bottom: 35px;
  }
  &__calculate {
    margin-left: 45px;
    & > a {
      display: inline-block;
      padding: 25px 75px;
      box-shadow: 0px 4px 0px 0px #eb9500;
      font-weight: 800;
      background-color: #cfcfd2;
      background-image: linear-gradient(45deg, $accent-color 34%, #ffce6e 65%);
      border-radius: 50px;
      transition: .25s;
    }
    &:hover a {
      box-shadow: 0px 0px 0px 0px #eb9500;
      transform: translateY(4px);
    }
  }
}

.features {
  display: flex;
  &__item {
    position: relative;
    z-index: 2;
    width: 200px;
    height: 250px;
    // margin-right: 1px;
    padding: 15px 25px;
    padding-right: 10px;
    border-radius: 5px;
    &::before, &::after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border-radius: 5px;
    }
    &::before {
      background-image: linear-gradient(45deg, #ffffff 2%, #d6e7f1 97%);
      z-index: -1;
      transform: skewX(-4deg);
      box-shadow: 0px 10px 25px 0px rgba(0,0,0,0.07);
    }
    &::after {
      background-repeat: no-repeat;
      background-position: center bottom;
      background-size: auto;
    }
    &--pool::after {
      background-image: url(../img/content/features/pool.png);
    }
    &--equipment::after {
      background-image: url(../img/content/features/equipment.png);
    }
    &--cleaning::after {
      background-image: url(../img/content/features/cleaning.png);
    }
    &--hands::after {
      background-image: url(../img/content/features/hands.png);
      left: -5px;
    }
  }
  &__text {
    position: relative;
    z-index: 2;
    font-weight: 300;
    color: #000000;
    & > span {
      font-size: 2.4rem;
    }
  }
}

.calculate-header {
  padding: 35px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__title {
    font-size: 3rem;
    font-weight: 900;
    text-transform: uppercase;
    margin-bottom: 15px;
    color: rgb(55,162,222);
    @supports (background-clip: text) {
      background: url(../img/bg/text_mask.png) no-repeat center / cover;
      color: transparent;
      background-clip: text;
    }
  }
  &__subtitle {
    font-size: 2rem;
    font-weight: 500;
    color: #444444;
  }
}
.works {
  background:url(../img/bg/bg2.jpg) no-repeat center / cover;
  padding-top: 600px;
  padding-bottom: 25px;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title {
    text-align: center;
    font-size: 3rem;
    font-weight: 900;
    text-transform: uppercase;
    margin-bottom: 20px;
  }
  &__subtitle {
    font-size: 2.2rem;
    font-weight: 500;
    color: #ffffff;
    text-align: center;
    margin-bottom: 40px;
  }
  &__gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 25px;
  }
  &__item {
    max-width: calc(25% - 3px);
    @supports (flex-basis: calc(25% - 3px)) {
      flex-basis: calc(25% - 3px)
    }
    margin-bottom: 5px;
    // & a {}
    & img {
      width: 100%;
      height: 100%;
    }
  }
  &__more {
    font-size: 2rem;
    font-weight: 400;
    color: #fefefe;
    display: flex;
    align-items: center;
    &::after {
      content: '';
      margin-top: 3px;
      border-top: 10px solid #ffffff;
      border-left: 12px solid transparent;
      border-right: 12px solid transparent;
      margin-left: 12px;
    }
  }
}


@import "blocks/footer";